.nav-links a:focus {
  background-color: #ffffff;

  border-top-style: solid;
  border-top-color: coral;
}

.content {
  display: flex;
  margin: auto;
  text-align: center;
}

.logoimg {
  height: 40px;
  width: 30px;
  margin-top: 5px;
}

.name-count h3 {
  color: #0366d6;
}
.name-tag {
  font-size: small;
}
.name-count-row {
  background-color: #fafbfc;
  padding: 10px 0 10px 0;
}
.navbar {
  margin-bottom: 0;
}
